import React, { Component } from "react";
import { Modal, Button, Divider } from "semantic-ui-react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import { closeModal, openModal } from "./modalActions";

const actions = { closeModal, openModal };

class UnauthModal extends Component {
  handleCloseModal = () => {
    if (this.props.location.pathname.includes("/event")) {
      this.props.closeModal();
    } else {
      this.props.history.goBack();
      this.props.closeModal();
    }
  };

  render() {
    const { openModal } = this.props;
    return (
      <Modal size="mini" open={true} onClose={this.handleCloseModal}>
        <Modal.Header>
              ,   !
        </Modal.Header>
        <Modal.Content>
          <Modal.Description>
            <p>
              ,   ,   
              
            </p>
            <Button.Group widths={4}>
              <Button
                fluid
                color="teal"
                onClick={() => openModal("LoginModal")}
              >
                
              </Button>
              <Button.Or text="" />
              <Button fluid positive onClick={() => openModal("RegisterModal")}>
                
              </Button>
            </Button.Group>
            <Divider />
            <div style={{ textAlign: "center" }}>
              <p>  "",    </p>
              <Button onClick={this.handleCloseModal}></Button>
            </div>
          </Modal.Description>
        </Modal.Content>
      </Modal>
    );
  }
}

export default withRouter(
  connect(
    null,
    actions
  )(UnauthModal)
);



